import React, { useState } from 'react';
import Quiz from 'react-quiz-component';
import './App.css';
import ExcelToJson from './components/ExcelToJson';
import Instruction from './components/Instruction';
import * as XLSX from 'xlsx';
import { instractionText } from "./instructionText"

function App() {
  const [reload, setReload] = useState(false);
  const [quiz, setQuiz] = useState(null);
  const [count, setCount] = useState(1);
  const [quizComplete, setQuizComplete] = useState(false);
  const [quizResult, setQuizResult] = useState(null);
  const [nameTyped, setNameTyped] = useState('');
  const [grade, setGrade] = useState(null);

  const getResult = (obj) => {
    setQuizComplete(true);
    setQuizResult(obj);

    //    
    let totalPoints = parseInt(obj.totalPoints);
    let correctPoints = parseInt(obj.correctPoints);

    //    
    let percentage = (correctPoints / totalPoints) * 100;

    //     
    let grade;
    switch (true) {
      case (percentage >= 80):
        grade = 5;
        break;
      case (percentage >= 60):
        grade = 4;
        break;
      case (percentage >= 40):
        grade = 3;
        break;
      case (percentage >= 20):
        grade = 2;
        break;
      default:
        grade = 1;
    }
    setGrade(grade);

    let h2Elements = document.getElementsByTagName('h2');
    if (h2Elements.length > 1 && !h2Elements[1].textContent.includes(':')) {
      h2Elements[1].textContent += ` : ${grade}`;
    }
  }

  const handleReload = () => {
    setReload(prevState => !prevState);
    setQuizResult(null);
    setQuizComplete(false);
    setGrade(null);
  }

  const handleCountChange = (e) => {
    setCount(e.target.value);
    handleReload();
    let numberOfQs = { "nrOfQuestions": null };
    numberOfQs["nrOfQuestions"] = String(e.target.value);

    if (quiz !== null) {
      setQuiz(prevState => ({ ...prevState, ...numberOfQs }));
    }
  }

  // const handleDataDownloadTXT = () => {
  //   if (!nameTyped) {
  //     alert('  ,     ');
  //     return;
  //   }

  //   let resultText = `  : ${nameTyped}\n`
  //   resultText += `  : ${quizResult.numberOfCorrectAnswers}\n`;
  //   resultText += `  : ${quizResult.numberOfIncorrectAnswers}\n`;
  //   resultText += ` : ${quizResult.numberOfQuestions}\n`;
  //   resultText += ` : ${quizResult.totalPoints}\n`;
  //   resultText += ` : ${quizResult.correctPoints}\n\n`;

  //   //     
  //   resultText += `: ${grade}\n\n`;

  //   quizResult.questions.forEach((question, index) => {
  //     resultText += ` ${index + 1}: ${question.question}\n`;
  //     resultText += ` :\n`;
  //     question.answers.forEach((answer, answerIndex) => {
  //       resultText += `   ${answerIndex + 1}) ${answer}\n`;
  //     })
  //     resultText += ` : ${question.correctAnswer}\n`;
  //     resultText += ` : ${quizResult.userInput[index]}\n\n`;
  //   });

  //   const blob = new Blob([resultText], { type: 'text/plain' });
  //   const url = URL.createObjectURL(blob);

  //   const link = document.createElement('a');
  //   link.href = url;
  //   link.download = 'quiz_results.txt';
  //   link.click();

  //   URL.revokeObjectURL(url);
  // };

  const handleDataDownloadXLSX = () => {
    if (!nameTyped) {
      alert('  ,     ');
      return;
    }

    let data = [
      { '': '  ', '': nameTyped },
      { '': '  ', '': quizResult.numberOfCorrectAnswers },
      { '': '  ', '': quizResult.numberOfIncorrectAnswers },
      { '': ' ', '': quizResult.numberOfQuestions },
      { '': ' ', '': quizResult.totalPoints },
      { '': ' ', '': quizResult.correctPoints },
      { '': '', '': grade }
    ];

    quizResult.questions.forEach((question, index) => {
      data.push({ '': ` ${index + 1}`, '': question.question });
      question.answers.forEach((answer, answerIndex) => {
        data.push({ '': `  ${answerIndex + 1}`, '': answer });
      })
      data.push({ '': ' ', '': question.correctAnswer });
      data.push({ '': ' ', '': quizResult.userInput[index] });
    });

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Quiz Results");
    XLSX.writeFile(workbook, 'quiz_results.xlsx');
  }

  let quizData = null;

  if (quiz !== null) {
    quizData = quiz;
  }

  const modalStyles = {
    button: 'button',
    conteiner: 'container'
  }

  return (
    <div className="app">
      <div style={{ display: 'flex', flexDirection: 'column', margin: '20px', minWidth: '37vw' }} id="first-column">
        <div className='container'>
          <Instruction styles={modalStyles} instractionText={instractionText} />
        </div>
        <div className="container">
          <label>: </label>
          <input
            className='input'
            style={{ marginLeft: '5px' }}
            type='text'
            placeholder=' '
            value={nameTyped}
            onChange={(e) => { setNameTyped(e.target.value) }}
          />
        </div>

        <div className="container" style={{ display: 'flex', flexDirection: 'column' }}>
          <div style={{ marginBottom: '10px' }}>
            <label style={{ marginRight: "10px" }}>   </label>
            <input
              className='input'
              style={{ width: '50px' }}
              type="number"
              value={count}
              onChange={handleCountChange}
              min="1"
            />
          </div>
          <span style={{ fontSize: '12px', marginBottom: '10px' }}>*     </span>
        </div>

        <div style={{ display: 'flex', flexDirection: 'column' }}>
          <div className="container" style={{ display: 'flex', flexDirection: 'column' }}>
            <ExcelToJson
              handleNewQuize={setQuiz}
              count={count}
              handleReload={handleReload}
              handleNewTry={setQuizComplete}
              handleNewResult={setQuizResult}
            />

            {/* <div style={{ marginTop: '20px', display: 'flex', flexDirection: 'column' }}>
              <span> JSON   </span>
              <button className='button' style = {{width: '145.19px', marginTop: '10px'}}></button>
            </div> */}
          </div>
        </div>


        <div className="container" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <button className="button" style={{ marginBottom: '15px', width: '200px' }} onClick={() => window.location.reload()}> </button>
          <button className="button" style={{ width: '200px' }} onClick={() => handleReload()}> </button>
        </div>

      </div>
      {quizData && <div security={{ display: 'flex', flexDirection: 'column' }}>
        <Quiz
          key={reload}
          quiz={quizData}
          shuffle={true}
          shuffleAnswer={true}
          showInstantFeedback={true}
          disableSynopsis={false}
          onComplete={getResult}
          showDefaultResult={true}
        />

        {quizComplete &&
          <button className='button' onClick={() => handleDataDownloadXLSX()} style={{ marginLeft: '20px', marginBottom: '50px', width: '200px' }}>
             
          </button>
        }
      </div>}
      <div style={{ width: '100%' }} id="fix" />
    </div>
  );
}

export default App;
